*{
  margin: 0;
}

body{
  background-color: #f5f6fa;
}
.app{
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  padding: 20px;
}
.app__header{
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  justify-content: space-between;
}

.app__stats{
  /* display: flex; */
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.app__left{
  flex:0.9
}

@media(max-width:900px){
  .app{
    flex-direction: column;
  }
}

@media(max-width:320px){
  .app__stats{
    flex-direction: column;
    text-align: center;
  }
}